router.beforeEach((to, from, next) => {
  // console.log('before each');
  // console.log(from);
  // console.log(to);
  //if ((to.path=='/login')||(globalStore.userLogged)) {
  if ((to.path=='/login') || window.$cookies.isKey('user')) {
    next( )
  }
  else {
    next('/login')
  }
})


export default router;



